const { DynamoDBDocumentClient, GetCommand } = require("@aws-sdk/lib-dynamodb");


export default function App() {

  async function getItems() {
    const client = new DynamoDBClient({ region: "us-west-2" });
    const ddbDocClient = DynamoDBDocumentClient.from(client);
    try {
      return await ddbDocClient.send(
          new GetCommand({
            TableName: "RetailDatabase",
            Key: {
              pk: "jim.bob@somewhere.com", // Partition Key
              sk: "metadata"          // Sort Key
            },
            // For this use case, the data does not changed often so why not get the
            // reads at half the cost? Your use case might be different and need true.
            ConsistentRead: false,
          })
      );
    } catch (err) {
      console.error(err);
    }
  }
  
  getItems()
      .then((data) =>
          console.log("GetCommand succeeded:", JSON.stringify(data, null, 2)))
      .catch((error) => console.error(JSON.stringify(error, null, 2)));


  //let ddbDocClient = new DynamoDB.DocumentClient({ region: "us-east-2", accessKeyId: 'AKIAVTQBHSP373NE63NH', secretAccessKey: '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' });
  var results = {};
  var ddbData = {}; 

  const client = new DynamoDBClient({ region: "us-east-2", accessKeyId: 'AKIAVTQBHSP373NE63NH', secretAccessKey: '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' });
  const ddbDocClient = DynamoDBDocumentClient.from(client);

  setTimeout(fetchDataFormDynamoDb, 1500); 

  async function fetchDataFormDynamoDb() {

    let params = {
      TableName: 'iotmonitor',
      Limit: 5,
      ScanIndexForward: false
    } 

      let result = await ddbDocClient.scan(params).promise().then((data) => {
       return data
     }) 

    // Now you can use result outside of the promise.
    console.log(JSON.stringify(result))
    ddbData = JSON.parse(JSON.stringify(result));
    console.log(ddbData);

    console.log(ddbData['Items'][0]['payload'].bearing);

    const data = [
      { count: ddbData['Items'][4]['id'], bearing: ddbData['Items'][4]['payload'].bearing },
      { count: ddbData['Items'][3]['id'], bearing: ddbData['Items'][3]['payload'].bearing },
      { count: ddbData['Items'][2]['id'], bearing: ddbData['Items'][2]['payload'].bearing },
      { count: ddbData['Items'][1]['id'], bearing: ddbData['Items'][1]['payload'].bearing },
      { count: ddbData['Items'][0]['id'], bearing: ddbData['Items'][0]['payload'].bearing }, 
    ];
 
    var ctx = document.getElementById('myChart').getContext('2d'); // 2d context 

    var myChart = new Chart(ctx, {
      type: 'line',
      data: {
        labels: data.map((data) => data.count),
        datasets: [
          {
            label: 'Acquisitions by year',
            data: data.map((data) => data.bearing),
            backgroundColor: [
              "rgba(75,192,192,1)",
              "#ecf0f1",
              "#50AF95",
              "#f3ba2f",
              "#2a71d0"
            ],
            borderColor: "black",
            borderWidth: 2
          }
        ]
      }
    }
    );  
  }; 

  return (
    <>
      <canvas id="myChart" width="400" height="400"></canvas>

      <div className="App">
        {/*<LineChart chartData={chartData} />*/}
      </div>
    </>
  );
}